<template>
    <div class="app">
        <div class="currencyName ov">
            <label class="fl" for="name">选择财富树币种区：</label>
            <input class="fl currencyN" readonly v-model="currency.name" type="text" id="name" placeholder="请选择">
            <router-link class="fr" to="/currency?redirect=treecreate" href="javascript:void(0);">切换币种</router-link>
        </div>
        <div class="currencyName ov">
            <label class="fl" for="caifu">财富树名称：</label>
            <input class="fl" v-model="createForm.name" type="text" id="caifu" placeholder="财富树名称">
        </div>
        <div class="hierarchy ov">
            <p class="fl">层级：</p>
            <div class="fr add">
                <img @click="changeLevel(-1)" src="@/static/images/tree/icon_cut.png" alt="">
                <span>{{createForm.level}}</span>
                <img @click="changeLevel(1)" src="@/static/images/tree/icon_add.png" alt="">
            </div>
        </div>
        <div class="currencyName ov">
            <label class="fl" for="money">第一层金额：</label>
            <input class="fl" v-model="createForm.money" type="text" id="money" placeholder="请输入金额">
        </div>
        <div class="hierarchy ov">
            <p class="fl">每层金额递增率</p>
            <div class="add fr">
                <img @click="changeRatio(-5)" src="@/static/images/tree/icon_cut.png" alt="">
                <span>{{createForm.growth_ratio}}%</span>
                <img @click="changeRatio(5)" src="@/static/images/tree/icon_add.png" alt="">
            </div>
        </div>
        <div class="getRedpack">
            <div class="getredTag">推广限制：</div>
            <div class="minGet">
                <input v-model="limit_type" value="1" type="radio" id="minlab" name="extension" />
                <label for="minlab">
                    <div class="number">
                        <img @click="changeLimit(-1)" src="@/static/images/tree/icon_cut.png" alt />
                        <span>一推{{limit}}</span>
                        <img  @click="changeLimit(1)" src="@/static/images/tree/icon_add.png" alt />
                    </div>
                </label>
            </div>
            <div class="maxGet">
                <input v-model="limit_type" value="2" type="radio" id="maxlab" name="extension" />
                <label for="maxlab">一推无数(太阳线)</label>
            </div>
        </div>
        <div class="statistics">
            <div class="holderNum pr"><p>本财富树构架</p></div>
            <div class="profitBox">
                <p class="pTitle">您的支出</p>
                <p class="pArt">往上1—{{createForm.level}}层付款{{createForm.level}}次，总花费{{createMoney}}个币</p>
                <ul class="profitList">
                    <li v-for="(val, index) in levelMoney"><p>升{{index+1}}级：给上面第{{index+1}}级{{val}}个币</p></li>
                </ul>
            </div>
            <div class="profitBox">
                <p class="pTitle">您的收入</p>
                <p class="pArt">往下1—{{createForm.level}}层收款{{lowTotalCount}}次，总收款{{lowTotalMoney}}个币</p>
                <ul class="profitList">
                    <li v-for="(item, index) in lowMoney"><p>下{{index+1}}级收取：{{item.count}}人 x {{item.money}} = {{item.total}}</p></li>
                </ul>
            </div>
            <p class="hArt">您的收益倍数:{{profittimes}}倍</p>
            <p class="hArt" v-if="limit_type == 2">注: 太阳线默认按照1推5来计算</p>
        </div>
        <div class="artBox">
            <p>创建此财富树需要缴纳{{createMoney}}个币的押金，创立成功后，您的位置属于金字塔顶端。</p>
            <p>押金退还机制：您创立的财富树收入超过押金金额后，可以申请退还您的押金。</p>
            <!--<p @click="moneyInfo = true">查看具体金额信息>></p>-->
        </div>
        <div class="sBtn"><button @click="createTree" :disabled="createdisabled">创建</button></div>


        <van-dialog
                v-model="moneyInfo"
        >
            <p style="font-size: 0.14rem;color: #333333;padding: 0.2rem;line-height: 0.2rem;overflow-y: scroll;max-height: 4rem;"><template v-for="(val, index) in levelMoney">升{{orderText[index]}}级{{val}}个币<br></template></p>
        </van-dialog>
    </div>

</template>

<script>
    import treeBar from '@/page/tree/treeBar'
    import { createTree } from '@/api/tree'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    import Dialog from 'vant/lib/dialog';
    import 'vant/lib/dialog/style';
    import fmath from '@/utils/math'
    import { isNumber } from "vant/es/utils/validate/number";
    import { currencyFirst, currencyInfo} from "@/api/index";
    export default {
        name: "Treecreate",
        components: {
            treeBar,
            [Dialog.Component.name]: Dialog.Component,
        },
        data()
        {
            return {
                currency_id:'',
                currency:{
                  name:'',
                  id:''
                },
                moneyInfo:false,
                orderText:['一', '二', '三', '四', '五', '六', '七', '八', '九', '十', '十一', '十二', '十三', '十四','十五', '十六', '十七', '十八'],
                levelMoney:[],
                lowMoney:[],
                limit_type:1,
                limit:2,
                createForm:{

                    currency_id:'',
                    name:'',
                    money:'',
                    growth_ratio:15,
                    level:1,
                    limit:2

                },
                createMoney:0,
                lowTotalMoney:0,
                lowTotalCount:0,
                profittimes:0,
                createdisabled: false
            }
        },
        watch:{

            limit(val)
            {
                if (this.limit_type == 1)
                {
                    this.createForm.limit = val

                }

            },
            limit_type(val)
            {
                if (this.limit_type == 1)
                {
                    this.createForm.limit = this.limit

                } else
                {
                    this.createForm.limit = 0
                }
            },
            createForm:
            {
                handler(val,oldval){

                    if (isNumber(this.createForm.money) && this.createForm.money > 0)
                    {
                       this.createMoney = this.getTreeUpgradeMoney(0, this.createForm.level,  this.createForm.money,  this.createForm.growth_ratio)
                    }
                    this.getLevelMoney()
                    this.getLowMoneyMoney()

                },
                immediate:false,
                deep:true
            },
            lowTotalMoney(val)
            {

                /** 收益倍数 */
                let money = fmath.floatDiv(this.lowTotalMoney, this.createMoney)
                let lth = fmath.decimalLength(money)
                if (lth >= 3)
                {
                    this.profittimes = money.toFixed(2)
                } else
                {
                    this.profittimes = money
                }


            }
        },
        created()
        {
            this.pageInit()
        },
        mounted()
        {

        },
        methods:{

            async pageInit()
            {

                this.currency_id = this.$route.query.currencyId
                /** 处理一下 默认先为1 */
                this.currency_id  =  this.currency_id ?  this.currency_id : 1;
                if (this.currency_id)
                {
                    this.createForm.currency_id = this.currency_id
                    let res = await currencyInfo(this.currency_id)
                    this.currency = res
                }


            },
            // 修改层级
            changeLevel(num)
            {
                this.createForm.level = this.createForm.level + num
                this.createForm.level = this.createForm.level <= 0 ? 1 : this.createForm.level
                this.createForm.level = this.createForm.level > 13 ? 13 : this.createForm.level

            },
            // 修改升级比例
            changeRatio(num)
            {
                this.createForm.growth_ratio = this.createForm.growth_ratio + num
                this.createForm.growth_ratio = this.createForm.growth_ratio < 5 ? 5 : this.createForm.growth_ratio

            },
            // 修改推广层级
            changeLimit(num)
            {
                if (this.limit_type == 1)
                {
                    this.limit = this.limit + num
                    this.limit = this.limit < 2 ? 2 : this.limit
                    this.createForm.limit = this.limit
                }

            },
            // 获取每层升级金额
            getLevelMoney()
            {
               this.levelMoney = []

                for (let i = 0; i <  this.createForm.level; i++)
                {
                    let money = 0
                    if (this.createForm.money > 0)
                    {
                        money = this.getTreeLevelMoeny(i+1, this.createForm.money,  this.createForm.growth_ratio)
                    } else
                    {
                        money = 0
                    }

                    this.levelMoney.push(money)
                }
            },

            /** 获取财富树 第n级的支付金额 */
            getTreeLevelMoenybeifen(level = 1, money, ratio)
            {
                for (let i = 1; i < level; i++)
                {
                    let addMoney = fmath.floatMul(ratio, money) / 100;
                    money = fmath.floatAdd(money, addMoney);
                }

                return money;
            },
            // 新规则
            getTreeLevelMoeny(level = 1, money, ratio)
            {

                var upmoney = money;
                let addMoney = fmath.floatMul(ratio, money) / 100;

                for (let i = 1; i < level; i++)
                {

                    upmoney = fmath.floatAdd(upmoney, addMoney)
                }

                return upmoney;
            },
            getTreeUpgradeMoney(level, tolevel, money, ratio)
            {
                let total = 0;
                level++;
                for (level; level <= tolevel; level++)
                {

                    if (level > 0)
                    {
                        total =  fmath.floatAdd(total, this.getTreeLevelMoeny(level, money, ratio));
                    }
                }
                let lth = fmath.decimalLength(total)
                if (lth >= 3)
                {
                    return total.toFixed(3);
                }
                return total
            },
            getLowMoneyMoney()
            {
                let limit =  0
                if (this.limit_type == 1)
                {
                    limit = this.limit

                } else
                {
                    limit = 5
                }
                this.lowMoney = []
                let lowTotalMoney = 0
                let lowTotalCount = 0
                for (let i = 0; i <  this.createForm.level; i++)
                {
                    let money = 0
                    let count = 0
                    if (this.createForm.money > 0)
                    {
                        money = this.getTreeLevelMoeny(i+1, this.createForm.money,  this.createForm.growth_ratio)

                    } else
                    {
                        money = 0
                    }
                    count = Math.pow(limit, i+1);
                    let total = fmath.floatMul(money, count)
                    lowTotalMoney = fmath.floatAdd(total, lowTotalMoney)
                    lowTotalCount = fmath.floatAdd(count, lowTotalCount)
                    let item = {}
                    item.money = money
                    item.total = total
                    item.count = count
                    this.lowMoney.push(item)
                }
                this.lowTotalMoney = lowTotalMoney
                this.lowTotalCount = lowTotalCount

            },
            createTree()
            {
                if (this.createForm.name == '' || this.createForm.name.length == 0)
                {
                    Toast('请输入财富树名称')
                    return
                }
                if (this.createForm.money == '' || this.createForm.money.length == 0)
                {
                    Toast('请输入第一层金额')
                    return
                }

                if (!isNumber(this.createForm.money))
                {
                    Toast('请输入合法金额')
                    return
                }

                if (this.createForm.money < 10)
                {
                    Toast('第一层金额不能少于10个币')
                    return
                }

                // 确认推广人数限制
                if (this.limit_type == 1)
                {
                    this.createForm.limit = this.limit

                } else
                {
                    this.createForm.limit = 0
                }

                Toast.loading({
                    duration: 0,       // 持续展示 toast
                    forbidClick: true, // 禁用背景点击
                    loadingType: 'spinner',
                });
                this.createdisabled = true
                createTree(this.createForm).then(res => {
                    Toast.clear()
                    Dialog.alert({
                        title: '创建财富树',
                        message: res.message ? res.message: '创建成功，快来邀请朋友加入吧！',
                        confirmButtonText:'我知道了'
                    }).then(() => {


                        this.$router.push('/treefinance?treeId='+res.tree_id)
                    });

                })


            }

        }
    }
</script>

<style scoped>
    @import "../../static/css/treecreate.css";
    .app .statistics .hArt{
        text-align: center;
        font-size: 0.1rem;
        color: #777676;
        margin: .1rem 0;
    }
</style>



